import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Landing from '@/components/Landing'
import Team from '@/components/Teams'
Vue.use(Router)

export default new Router({

    scrollBehavior (to, from, savedPosition) {
        if (to.hash) {
            return {
                selector: to.hash
            }
        }
    },
    routes: [
        {
            path: '/home',
            name: 'Home',
            component: Home
        },
        {
            path: '/',
            name: 'Landing',
            component: Landing
        },
        {
            path: '/team',
            name: 'Team',
            component: Team
        }
    ]
})



