<template>
    <transition name="fade">
        <div class="menu-bar">
            <div class="heading" v-if="$route.path != '/'">
                <div v-on:click="toggleMenuIfOpen">
                    <transition name="fade">
                        <router-link to="/home" v-if="!scrolled" :class="{white_bg: ! box_open}" class="riotact-logo">
                            RIOT<br> ACT

                        </router-link>
                    </transition>
                    <transition name="slide">
                        <div class="right" v-if="!scrolled">
                        <router-link class="menu-link" to="/about" v-if="!scrolled">About</router-link>
                        <a class="menu-link" @click="prod_open = false; team_open = !team_open" v-if="!scrolled">Team</a>
                        <a class="menu-link" @click="team_open = false; prod_open = !prod_open" v-if="!scrolled">History</a>
                        <router-link class="menu-link" to="/store" v-if="!scrolled">Store</router-link>
                        <router-link class="menu-link" to="/media" v-if="!scrolled">Videos</router-link>
                        <router-link class="menu-link" to="/contact" v-if="!scrolled">Contact</router-link>
                        </div>
                    </transition>
                    <transition name="slide-down">
                        <div v-if="team_open" class="dropdown-team">
                            <p><router-link to="/team">Whit Hertford</router-link></p>
                            <p><router-link to="/team">Actor 2</router-link></p>

                            <p><router-link to="/team">Actor 3</router-link></p>
                            <p><router-link to="/team">Actor 4</router-link></p>


                        </div>
                    </transition>
                    <transition name="slide-down">
                        <div v-if="prod_open" class="dropdown-team">
                            <p><router-link to="/productions">Star Wars</router-link></p>
                            <p><router-link to="/productions">Star Wars</router-link></p>

                            <p><router-link to="/productions">Star Wars</router-link></p>
                            <p><router-link to="/productions">Star Wars</router-link></p>


                        </div>
                    </transition>
                </div>

                <transition name="fade">
                    <a :class="{rotate: open}" v-on:click="toggleMenu" v-if="! btn_scrolled"
                       class="menu-button fa fa-bars"></a>
                </transition>


                <div class="box-con" v-if="box_open">

                    <div class="box">
                        <transition name="slide-down-m1">
                            <div id="1" class="menu-block light" v-if="open">
                                <div class="menu-block-container">
                            <span v-on:click="toggleMenu">
                            <router-link to="/about">About</router-link>
                            </span>
                                    <p class="menu-block-text">Founded 1717. Reestablished 2015.</p>
                                </div>
                            </div>
                        </transition>
                    </div>

                    <div class="box">
                        <transition name="slide-down-m2">

                            <div id="2" class="menu-block darker" v-if="open">
                                <div class="menu-block-container">
                            <span v-on:click="toggleMenu">
                            <router-link to="/team">Team</router-link>
                                </span>
                                    <p class="menu-block-text">The RIOT ACT Team.</p>

                                </div>
                            </div>
                        </transition>

                    </div>

                    <div class="box">
                        <transition name="slide-down-m3">
                            <div id="3" class="menu-block darkest" v-if="open">
                                <div class="menu-block-container">
                            <span v-on:click="toggleMenu">
                            <router-link to="/productions">History</router-link>
                            </span>
                                    <p class="menu-block-text">Our Production History.</p>
                                </div>
                            </div>
                        </transition>
                    </div>

                    <div class="box">
                        <transition name="slide-up-m6">

                            <div id="4" class="menu-block light" v-if="open">
                                <div class="menu-block-container">
                            <span v-on:click="toggleMenu">
                            <router-link to="/store">Store</router-link>
                                </span>
                                    <p class="menu-block-text">Buy things from us.</p>

                                </div>
                            </div>
                        </transition>
                    </div>

                    <div class="box">
                        <transition name="slide-up-m5">
                            <div id="5" class="menu-block darker" v-if="open">
                                <div class="menu-block-container">
                            <span v-on:click="toggleMenu">
                                <router-link to="/media">Media</router-link>
                            </span>
                                    <p class="menu-block-text">Videos and Images.</p>

                                </div>
                            </div>
                        </transition>
                    </div>

                    <div class="box">
                        <transition name="slide-up-m4">
                            <div id="6" class="menu-block darkest" v-if="open">
                                <div class="menu-block-container">
                            <span v-on:click="toggleMenu">
                            <router-link to="/contact">Contact</router-link>
                            </span>
                                    <p class="menu-block-text">Get in Touch.</p>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    export default{
        data(){
            return {
                open: false,
                box_open: false,
                is_home: false,
                scrolled: false,
                btn_scrolled: true,
                prod_open: false,
                team_open: false
            }
        },
        watch: {},
        created(){
            this.scrollListener();
        },
        methods: {
            scrollListener(){
                window.addEventListener("scroll", function () {

                    if ($(window).scrollTop() > 10) {
                        this.btn_scrolled = false;

                        if (!this.box_open) {
                            this.scrolled = true;
                        }
                        else {
                            this.scrolled = false;
                        }
                    }
                    else {
                        this.scrolled = false;
                        this.btn_scrolled = true;
                        this.team_open = false;
                        this.prod_open = false;


                    }


                }.bind(this));
                this.setScrollStatus();
            },

            setScrollStatus(){
                if ($(window).scrollTop() > 10) {
                    this.scrolled = true;
                    this.btn_scrolled = false;
                }
                else {
                    this.scrolled = false;
                    this.btn_scrolled = true;

                }

            },

            lockScroll(){
                this.scrolled = false;
            },
            toggleMenu(){
                let vm = this;

                if (!vm.box_open) {
                    vm.box_open = !vm.box_open;
                    vm.scrolled = false;
                    vm.lockScroll();
                    setTimeout(function () {
                        vm.open = !vm.open;

                    }, 50);
                }
                else {
                    vm.open = !vm.open;
                    this.setScrollStatus();
                    setTimeout(function () {
                        vm.box_open = !vm.box_open;


                    }, 700);
                }
            },

            toggleMenuIfOpen(){
                let vm = this;
                if (vm.open) {
                    vm.toggleMenu();
                    vm.setScrollStatus();
                    vm.scrollListener();
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    @import '../assets/angled-edge';

    .dropdown-team{
        position: absolute;
        width: 100%;
        max-height: 100vh;
        background: $background;
        top: 10em;
        z-index: 2;
        overflow: hidden;
        text-align: right;
        padding: 1em 0 ;
    a{
        font-variant: all-small-caps;
        margin: .25em;
        position: relative;
        top: .5em;
        font-size: 2em;
        font-family: 'Avenir Heavy', sans-serif;


    }
    }




    .menu-link{
        position: relative;
        float: left;
        margin: .5em .25em;
        font-size:1.75em;
        font-variant: all-small-caps;
        font-family: 'Avenir Heavy', sans-serif;
        cursor: pointer;
    }

    .right{
        right:2.5em;
        position: absolute;
        bottom:0;
    }

    .slide-down-leave-to{
        max-height: 0;
    }

    .slide-down-enter{
        max-height: 0;
    }

    .slide-down-enter-active, .slide-down-leave-active {
        transition: all 1s;

    }

    .slide-enter-to{
        opacity: 1;
    }

    .riotact-logo {
        width: auto;
        height: auto;
        font-family: 'Avenir Next Bold', sans-serif;
        font-size: 4em;
        color: white;
        position: fixed;
        z-index: 4;
        top: calc(.25em);
        line-height: .85;
        transition: all .3s;
        text-decoration: none !important;
        padding: .25em;
        padding-left: .75em;
        text-align: right;

    }

    .riotact-logo:hover {
        text-decoration: none;
    }

    .white_bg {
        color: black;
        position: absolute;
    }

    .slide-down-m1-enter, .slide-down-m1-enter-to, .slide-down-m2-enter, .slide-down-m2-enter-to, .slide-down-m3-enter, .slide-down-m3-enter-to {
        left: -33.33vw
    }

    .slide-up-m4-enter, .slide-up-m4-enter-to, .slide-up-m5-enter, .slide-up-m5-enter-to, .slide-up-m6-enter, .slide-up-m6-enter-to {
        right: -33.33vw
    }

    .slide-down-m1-enter-active {
        animation: slide-bottom .7s;
    }

    .slide-down-m1-leave-active {
        animation: slide-bottom reverse .7s
    }

    .slide-down-m2-enter-active {
        animation: slide-bottom .7s;
        animation-delay: .1s;

    }

    .slide-down-m2-leave-active {
        animation: slide-bottom reverse .7s
    }

    .slide-down-m3-enter-active {
        animation: slide-bottom .7s;
        animation-delay: .2s;
    }

    .slide-down-m3-leave-active {
        animation: slide-bottom reverse .7s
    }

    .slide-up-m4-enter-active {
        animation: slide-top .7s;
        animation-delay: .1s;

    }

    .slide-up-m4-leave-active {
        animation: slide-top reverse .7s
    }

    .slide-up-m5-enter-active {
        animation: slide-top .7s;
        animation-delay: .2s;

    }

    .slide-up-m5-leave-active {
        animation: slide-top reverse .7s
    }

    .slide-up-m6-enter-active {
        animation: slide-top .7s;
        animation-delay: .3s;
    }

    .slide-up-m6-leave-active {
        animation: slide-top reverse .7s
    }

    @keyframes slide-bottom {
        0% {
            left: -33.33vw;
        }

        100% {
            left: 0;
        }
    }

    @keyframes slide-top {
        0% {
            right: -33.33vw;
        }

        100% {
            right: 0;
        }
    }

    .menu-button {
        z-index: 4;
        position: fixed;
        width: 1em;
        height: 1em;
        right: 1em;
        background: $background;
        top: .5em;
        border-radius: 50%;
        text-align: center;
        padding: 1em;
        font-size: 1.5em;
        transition: all .2s;
        color: black;
        box-sizing: content-box;
    }

    .menu-button:hover {
        text-decoration: none;
        cursor: pointer;
    }

    .rotate {
        transform: rotate(90deg);
    }

    .menu-bar {
        width: 100vw;
        height: 10em;
        position: relative;
    }

    .box {
        position: relative;
        top: 0;
        overflow: hidden;
        width: 33.33vw;
        height: 50vh;
        float: left;
        z-index: 3;
    }

    .box-con {
        width: 100%;
        height: 100vh;
        position: fixed;
        z-index: 3;
    }

    .menu-block {
        width: 100%;
        height: 100%;
        position: absolute;
        background: #444444;
        padding: 1em;
    }

    .menu-block-container a {
        color: white;
        font-size: 2.5em;
        font-family: 'Avenir Heavy', sans-serif;
        font-variant: all-small-caps;
        position: absolute;
        text-align: center;
        width: 5em;
        height: 2em;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
    }

    .menu-block-container a::after {
        content: '.'
    }

    .menu-block-text {
        color: white;
        width: 80%;
        height: 2em;
        position: absolute;
        left: 0;
        right: 0;
        top: 4em;
        margin: auto;
        font-size: 1.25em;
        opacity: 0;
        transition: all .5s;
        bottom: .5em;
    }

    .menu-block-container:hover .menu-block-text {
        opacity: 1;
        bottom: 0;

    }

    .menu-block-container {
        text-align: center;
        width: 75%;
        height: 100%;
        left: 12.5%;
        position: relative;
        overflow: hidden;
    }

    .light {
        background: #2c2c2c;
    }

    .darker {
        background: #1e1e1e;
    }

    .darkest {
        background: #191919;
    }

    .invisible {
        background: transparent;
    }

    @media screen and (max-width: 800px) {
        .menu-link{
            display:none;
        }
    }

        @media screen and (max-width: 1024px) {

        .slide-down-m1-enter, .slide-down-m1-enter-to, .slide-down-m2-enter, .slide-down-m2-enter-to, .slide-down-m3-enter, .slide-down-m3-enter-to {
            top: -16.66vh;
            left: 0;
            right: 0;
        }

        .slide-up-m4-enter, .slide-up-m4-enter-to, .slide-up-m5-enter, .slide-up-m5-enter-to, .slide-up-m6-enter, .slide-up-m6-enter-to {
            top: -16.66vh;
            left: 0;
            right: 0;
        }

            .menu-link{
                margin: 1em .25em;
                left: 5.5em;
            }

            .menu-button {
                right: .25em;
                top: .25em;

            }

        .box {
            width: 100%;
            height: calc(100vh / 6);
        }

        .box-con {
            overflow: hidden;
        }

        .menu-block-text {
            opacity: 1;
            font-size: .75em;
        }



        .riotact-logo {
            font-size: 2em;
        }

        .menu-block-text:hover {
            bottom: .5em;
        }



        @keyframes slide-bottom {
            0% {
                top: -16.66vh;
            }

            100% {
                top: 0;
            }
        }

        @keyframes slide-top {
            0% {
                top: -16.66vh;
            }

            100% {
                top: 0;
            }
        }
    }


</style>


