<template>
    <div>
        <menubar></menubar>
        <div id="app">
            <transition name="slide">
                <router-view class="wrapper"></router-view>
            </transition>
        </div>

    </div>
</template>

<script>
    import menubar from './components/Menu'
    export default {
        components: {
            menubar
        },
        name: 'app'
    }
</script>

<style lang="scss">

    ::-webkit-scrollbar{
        display: none;
    }

   .slide-leave-to{
        transform: translatex(-100vw);
        opacity: 0;
    }

   .slide-enter{
       transform: translatex(100vw);

   }

    .slide-enter-active, .slide-leave-active {
        transition: all 1s;

    }

    .slide-enter-to{
        opacity: 1;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
        opacity: 0
    }


    * {
        box-sizing: border-box;
        padding: 0;
        margin: 0;
        font-family: 'Avenir Light', sans-serif;
        color: #0a0a0a;
        text-decoration: none;
    }

    h1 {
        font-family: 'Avenir Heavy', sans-serif;
        font-variant: all-small-caps;
        font-size: 5em;
    }

    @font-face {
        font-family: 'Avenir Next Demi';
        src: url(./assets/fonts/AvenirNextLTPro-Demi.otf) format("opentype");
    }
    @font-face {
        font-family: 'Avenir Light';
        src: url(./assets/fonts/AvenirLTStd-Light.otf) format("opentype");
    }

    @font-face {
        font-family: 'Avenir Book';
        src: url(./assets/fonts/AvenirLTStd-Book.otf) format("opentype");
    }

    @font-face {
        font-family: 'Avenir Heavy';
        src: url(./assets/fonts/AvenirLTStd-Heavy.otf) format("opentype");
    }

    @font-face {
        font-family: 'Avenir Next Bold';
        src: url(./assets/fonts/AvenirNextLTPro-Bold.otf) format("opentype");
    }

    @import 'assets/angled-edge';

    .wrapper {
        width: calc(100vw - 20em);
        margin: 0 10em;
        height: auto;
        min-height: 100vh;
        left: 0;
        position: absolute;
    }


    @media screen and (max-width: 1024px){
        .wrapper{
            width: 100vw;
            margin:0;
        }

        .background{
            width: 100%;
        }
    }


</style>



