<template>
    <div>
        <h1 class="title">Season <span>{{currentDate}}</span>.</h1>
        <div class="show-bar">
            <a class="show-box" @click="id = show.id; scrollTo" v-for="show in upcoming">
                <h2>{{show.title}}</h2>
                <p>{{show.short}}</p>
                <h3>{{show.date}} -<br> {{show.end}}</h3>
            </a>
        </div>

        <transition name="fade2">
            <div class="background" :key="upcoming[id]"
                 :style="'background: url(' + upcoming[id].src + ') 50% / cover'">
                <h2>{{upcoming[id].short}}</h2>
            </div>
        </transition>
        <transition name="fade2">

            <h1 :key="upcoming[id]">{{upcoming[id].title}}</h1>
        </transition>

    </div>
</template>
<script>
    import axios from 'axios';
    import vueSmoothScroll from 'vue-smoothscroll';

    export default{
        data(){
            return {
                id: 0,
                currentDate: 1,
                upcoming: [{
                    id: 0,
                    title: 'HØÜSES',
                    date: 'April 6th',
                    end: 'April 7th',
                    short: "STAR-CROSSED LUVRS♀ ♀",
                    src: 'https://scontent.xx.fbcdn.net/v/t35.0-12/17692493_10154507221632333_1735472698_o.jpg?oh=a3be0d444ed08e9b6eba4cf086398d24&oe=58E84233',
                    body: "Bob is your regular everyday guy, except he isn't because I'm writing this damn story and I decide how special bob is. But that's not the point. Our good pal Bob decides to go on some kind of adventure that I've yet to decide. To be continued when I've had my morning coffee."

                },
                    {
                        id: 1,
                        title: 'BLINDNESS AND PERIPHERY OF SIGHT',
                        date: 'April 7th',
                        end: 'April 8th',
                        short: 'THE STAINS OF MEMORY',
                        src: 'http://www.slugmag.com/wp/wp-content/uploads/2017/01/PoorBastard4.jpg',
                        body: ""


                    },
                    {
                        id: 2,

                        title: 'HATEFUL DEEDS',
                        date: 'April 8th',
                        end: 'April 9th',

                        short: 'A  TREMENDOUS TRAGEDY. BELIEVE ME.',
                        src: 'http://www.slugmag.com/wp/wp-content/uploads/2017/01/PoorBastard3.jpg',
                        body: ""


                    },
                    {
                        id: 3,

                        title: 'AN ENEMY OF THE PEOPLE',
                        date: 'April 9th',
                        end: 'April 10th',
                        short: "MAJORITY DOESN'T RULE.!",
                        src: 'https://blogs-images.forbes.com/brandonkatz/files/2016/10/Star-Wars-1200x675.jpg',
                        body: "It is a period of civil war. Rebel spaceships, striking from a hidden base, have won their first victory against the evil Galactic Empire.                        During the battle, Rebel spies managed to steal secret plans to the Empire's ultimate weapon, the DEATH STAR, an armored space station with enough power to destroy an entire planet.                    Pursued by the Empire's sinister agents, Princess Leia races home aboard her starship, custodian of the stolen plans that can save her people and restore freedom to the galaxy...."


                    }],
                riot_act: [],
                riot_act_title: [],
                about_us: []
            }
        },

        created(){
            this.getSnippets();
        },
        methods: {

            scrollTo() {
                let element_to_scroll_to = document.getElementById('app');

// Basically `element_to_scroll_to` just have to be a reference
// to any DOM element present on the page
// Then:
                element_to_scroll_to.scrollIntoView();
            },


            getSnippets(){
                let vm = this;
                axios.get('http://content.demo.studiocadogan.com/api/print/snippet/1').then(function (response) {
                    vm.$set(vm, 'riot_act', response.data.body);
                }).then(
                    function () {
                        axios.get('http://content.demo.studiocadogan.com/api/print/snippet/2').then(function (response) {
                            vm.$set(vm, 'riot_act_title', response.data.body);
                        })
                    }
                ).then(function () {
                    axios.get('http://content.demo.studiocadogan.com/api/print/snippet/3').then(function (response) {
                        vm.$set(vm, 'about_us', response.data.body);
                    })
                }).then(function () {
                    vm.id = vm.upcoming[0].id;

                    var d = new Date;
                    vm.currentDate = d.getFullYear().toString().substr(-2);
                })
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    @import '../assets/angled-edge';

    .title {
        font-variant: all-small-caps;
        font-size: 4em;
        text-align: right;
        position: fixed;
        left: -1em;
        top: 5em;
        transform: rotate(-90deg);

        span {
            font-size: 1.5em;
            letter-spacing: -.1em;
            -webkit-text-stroke: 1px black;
            color: transparent;

            font-family: 'Avenir Next Demi', sans-serif;

        }
    }

    .background {
        width: 100%;
        height: 30em;
        margin: 0 5em 0 0;
        @include angled-edge('inside bottom', 'upper right', $background);
        h2 {
            position: absolute;
            bottom: -.5em;
            width: 70%;
            font-size: 3em;
            z-index: 2;
            font-variant: all-small-caps;
            font-family: 'Avenir Heavy', sans-serif;

        }
    }

    .show-bar {
        width: 100%;
        height: 10.5em;
        position: relative;
        margin: 2em 0 0 0;
    }

    .show-box {
        height: 100%;
        width: 25%;
        position: relative;
        float: left;
        padding: 0 1.5em;
        transition: border .5s;
        border-bottom: 5px solid transparent;

        &:hover {
            border-bottom: 5px solid #212121;
        }
        h2 {
            border-bottom: 2px solid;
            position: relative;
            bottom:0;
            text-align: right;
            font-size: 1.4em;
            width: 100%;
            font-variant: all-small-caps;
            font-family: 'Avenir Next Demi', sans-serif;
        }

        p {
            font-family: 'Avenir Light', sans-serif;
            font-variant: all-small-caps;
            font-size: 1em;
        }

        h3 {
            position: absolute;
            bottom: 0;
            font-size: .9em
        }
    }

    .fade2-enter-active, .fade2-leave-active {
        transition: opacity .5s
    }

    .fade2-enter {
        opacity: 0
    }

    .fade2-leave-to {
        animation: fade-out reverse 1s;

    }

    .fade2-enter-to {
        animation: fade-out 1s;

    }

    @keyframes fade-out {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }

    }

</style>



